import { Injectable } from '@angular/core';
import { catchError, Observable, retry } from 'rxjs';
import { ILoginData, IUserData } from '../../interfaces';
import { SKIP_TOKEN } from '../../utilities/http-context';
import { HttpClient, HttpContext } from '@angular/common/http';
import { IGlobalResponse } from '../../interfaces/auth/gloal-res.interface';
import { AuthContextService } from './auth-context.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root',
})
export class AuthBeService {
  constructor(
    private http: HttpClient,
    private authContextService: AuthContextService,
    private router: Router
  ) {}

  login(data: {
    username: string;
    password: string;
  }): Observable<IGlobalResponse<ILoginData>> {
    return this.http.post<IGlobalResponse<ILoginData>>(
      'http://k8s-disbacke-apigatew-e17f595d6c-1283670367.us-east-1.elb.amazonaws.com/api/v1/idm/auth/login',
      { username: data.username, password: data.password },
      {
        context: new HttpContext().set(SKIP_TOKEN, true),
      }
    );
  }

  logout(): Observable<IGlobalResponse<any>> {
    return this.http.post<IGlobalResponse<any>>(
      'http://k8s-disbacke-apigatew-e17f595d6c-1283670367.us-east-1.elb.amazonaws.com/api/v1/idm/auth/logout',
      {}
    );
  }

  refreshToken(refreshToken: {
    refreshToken: string;
  }): Observable<IGlobalResponse<ILoginData>> {
    return this.http.post<IGlobalResponse<ILoginData>>(
      'http://k8s-disbacke-apigatew-e17f595d6c-1283670367.us-east-1.elb.amazonaws.com/api/v1/idm/auth/refresh',
      refreshToken,
      {
        context: new HttpContext().set(SKIP_TOKEN, true),
      }
    );
  }

  validateToken(): Observable<IGlobalResponse<any>> {
    return this.http
      .post<IGlobalResponse<any>>(
        'http://k8s-disbacke-apigatew-e17f595d6c-1283670367.us-east-1.elb.amazonaws.com/api/v1/idm/auth/validate',
        {}
      )
      .pipe(
        retry(3),
        catchError((error) => {
          console.error('Request failed after 3 retries', error);
          this.authContextService.clearData();
          window.dispatchEvent(new CustomEvent('auth-logout'));
          this.router.navigate(['/auth/login']);
          throw error;
        })
      );
  }

  getCurrUser(): Observable<IGlobalResponse<IUserData>> {
    return this.http.get<IGlobalResponse<IUserData>>(
      'http://k8s-disbacke-apigatew-e17f595d6c-1283670367.us-east-1.elb.amazonaws.com/api/v1/idm/auth/me'
    );
  }
}
