import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CustomDetailsHeaderComponent } from '../custom-details-header/custom-details-header.component';

@Component({
  selector: 'custom-details-modal',
  imports: [CommonModule, CustomDetailsHeaderComponent],
  templateUrl: './custom-details-modal.component.html',
  styleUrl: './custom-details-modal.component.css',
})
export class CustomDetailsModalComponent {
  @Input() modalTitle = '';
  @Input() overlayClickClose: boolean = true;
  @Input() BreadCrumbs!: { label: string; url: string }[];
  @Output() headerButtonClick = new EventEmitter<void>();

  isVisible = false;

  open() {
    this.isVisible = true;
  }

  close() {
    this.isVisible = false;
    // this.hideEvent.emit();
  }

  onOverlayClick(event: MouseEvent) {
    if (event.target === event.currentTarget && this.overlayClickClose) {
      this.close();
    }
  }
}
