import { CommonModule } from '@angular/common';
import {
  Component,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
} from '@angular/core';
import { ITabDropdownOption } from '../../interfaces';

@Component({
  selector: 'custom-tabs',
  imports: [CommonModule],
  templateUrl: './custom-tabs.component.html',
  styleUrl: './custom-tabs.component.css',
})
export class CustomTabsComponent {
  @Input({ required: true }) tabsList!: ITabDropdownOption[];
  @Input() color: string = '#4B4B4B';
  @Input() colorSelected: string = '#15C5CE';
  @Input() tabTemplates: { [key: string]: TemplateRef<any> } = {};
  @Output() tabSelected: EventEmitter<ITabDropdownOption> =
    new EventEmitter<ITabDropdownOption>();
  @Input() selectedTab!: ITabDropdownOption;

  ngOnInit() {
    if (!this.selectTab) this.selectedTab = this.tabsList[0];
  }

  selectTab(tab: ITabDropdownOption) {
    this.selectedTab = tab;
    this.tabSelected.emit(tab);
  }
}
